@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

h1 {
  font-size: 44px !important;
}

h2 {
  font-size: 35px !important;
  color: black;
}

main {
  overflow-x: hidden !important;
}

.section-wrapper {
  max-width: 1280px !important;
}

.link-header ol {
  column-count: 2;
  column-gap: 20px;
}

.box-header {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.box-header .number {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #177e89;
  border-radius: 50%;
  width: 42px;
  height: 42px;
  text-align: center;
  margin: 0.8rem;
  font-size: 2rem;
  font-family: "Noto Serif", serif !important;
}

.image-text {
  display: flex;
  align-items: center;
}

.text-box-left {
  border-radius: 15px;
  background-color: white;
  box-shadow: #113563 -15px -15px, #2b2b2b 10px 10px 15px;
  padding: 2.1rem 2.4rem !important;
  z-index: 2;
  height: fit-content;
}

.image-box-right {
  display: flex;
  align-items: center;
}

.image-box-right img {
  height: auto;
  max-width: 65vw;
  margin-left: -4rem;
  z-index: 1;
}

.text-box-right {
  border-radius: 15px;
  background-color: white;
  box-shadow: #113563 15px -15px, #2b2b2b -10px 10px 15px;
  padding: 2.1rem 2.4rem !important;
  z-index: 2;
  height: fit-content;
}

.image-box-left {
  display: flex;
  align-items: center;
}

.image-box-left img.displayed-image {
  height: auto;
  max-width: 65vw;
  position: absolute;
  right: -5vw;
  z-index: 1;
}

.image-box-left img.layout-placeholder {
  height: auto;
  max-width: 65vw;
  z-index: 1;
  visibility: hidden;
}

.block-heading {
  color: black;
  line-height: 0.6;
}

.block-heading-small {
  font-size: 1.3rem !important;
}

.bullet-point-title {
  text-align: center;
}

.bottom-text {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
  margin: auto;
}

@media (min-width: 1200px) {
  .bottom-text {
    max-width: 60%;
  }
}

.bottom-text p {
  text-align: left;
}

.bottom-text .fancy-hr {
  align-self: center !important;
}

.fancy-hr {
  margin: 0;
  width: 80%;
}

.fancy-hr img {
  max-width: 45px;
  margin: 1.2rem;
}

.fancy-hr:before, .fancy-hr:after {
  background: gray;
}

.page-title:before {
  color: #177e89;
  content: "//";
  font-size: 3.5rem;
  left: -1rem;
  letter-spacing: -6px;
  position: relative;
}

.activities-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
}

.activities-list li {
  vertical-align: middle;
  margin: 15px 30px !important;
}

@media (max-width: 768px) {
  h1 {
    font-size: 30px !important;
  }
  h2 {
    font-size: 26px !important;
  }
  .activities-list {
    grid-template-columns: repeat(1, 1fr);
  }
  .block-heading {
    line-height: 0.9 !important;
  }
  .page-title:before {
    font-size: 30px !important;
    left: -5px !important;
    letter-spacing: -1px !important;
  }
  .image-box-right, .image-box-left {
    padding-top: 15px;
  }
  .image-box-right img, .image-box-left img {
    max-width: 100% !important;
    margin: unset !important;
    border-radius: 15px !important;
    box-shadow: -10px -10px #084C61 !important;
    right: unset !important;
    position: relative !important;
  }
  .image-box-right .layout-placeholder, .image-box-left .layout-placeholder {
    display: none !important;
  }
  .text-box-left, .text-box-right {
    box-shadow: -10px -10px 15px #2b2b2b !important;
  }
  .activities-block:nth-child(even) .image-text {
    flex-direction: column-reverse !important;
  }
}
